<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.roomName" placeholder=""></el-input>
				</el-form-item>
				
				<el-form-item>
					<el-button type="primary" v-on:click="getRooms"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="rooms" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="userName" label="" width="auto">
			</el-table-column>
			<el-table-column prop="mobile" label="" width="auto">
			</el-table-column>
			<el-table-column prop="startHour" label="" width="auto">
			</el-table-column>
			<el-table-column prop="endHour" label="" width="auto">
			</el-table-column>

			<el-table-column prop="way" label="" :formatter="getWayDesc"  width="auto">
			</el-table-column>
			<el-table-column prop="defaultContent" label=""  :show-overflow-tooltip="true" width="auto">
			</el-table-column>

			<el-table-column prop="roomName" label="" width="auto">
			</el-table-column>

			<el-table-column prop="intervalTime" label="()" width="auto">
			</el-table-column>

			<el-table-column prop="url" label="" width="auto">
			</el-table-column>

			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item prop="roomName" label="">
					<el-input v-model="editForm.roomName" placeholder=""></el-input>
				</el-form-item>
				<el-form-item prop="mobile" label="">
					<el-input v-model="editForm.mobile" placeholder="11"></el-input>
				</el-form-item>
				<el-form-item prop="startHour" label="">
					<el-select v-model="editForm.startHour" placeholder="">
						<el-option v-for="item in hours" :key="item.id" :value="item.id" :label="item.desc"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item prop="endHour" label="">
					<el-select  v-model="editForm.endHour" placeholder="">
						<el-option v-for="item in hours" :key="item.id" :value="item.id" :label="item.desc"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item prop="way" label="">
					<el-select v-model="editForm.way" placeholder="">
						<el-option v-for="item in ways" :key="item.id" :value="item.id" :label="item.desc"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item prop="intervalTime" label="">
					<el-input v-model="editForm.intervalTime" placeholder="()"></el-input>
				</el-form-item>
				<el-form-item prop="url" label="">
					<el-input type="textarea" v-model="editForm.url" placeholder=""></el-input>
				</el-form-item>
				<el-form-item prop="defaultContent" label="">
					<el-input type="textarea" v-model="editForm.defaultContent" placeholder=""></el-input>
				</el-form-item>


			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item prop="roomName" label="">
					<el-input v-model="addForm.roomName" placeholder=""></el-input>
				</el-form-item>
				<el-form-item prop="mobile" label="">
					<el-input v-model="addForm.mobile" placeholder="11"></el-input>
				</el-form-item>
				<el-form-item prop="startHour" label="">
					<el-select v-model="addForm.startHour" placeholder="">
						<el-option v-for="item in hours" :key="item.id" :value="item.id" :label="item.desc"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item prop="endHour" label="">
					<el-select v-model="addForm.endHour" placeholder="">
						<el-option v-for="item in hours" :key="item.id" :value="item.id" :label="item.desc"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item prop="way" label="">
					<el-select v-model="addForm.way" placeholder="">
						<el-option v-for="item in ways" :key="item.id" :value="item.id" :label="item.desc"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item prop="intervalTime" label="">
					<el-input v-model="addForm.intervalTime" placeholder="()"></el-input>
				</el-form-item>
				<el-form-item prop="url" label="">
					<el-input type="textarea" v-model="addForm.url" placeholder=""></el-input>
				</el-form-item>
				<el-form-item prop="defaultContent" label="">
					<el-input type="textarea" v-model="addForm.defaultContent" placeholder=""></el-input>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
    import util from '../../common/js/util';
    import NProgress from 'nprogress';
    import {addRoom, delRoom, editRoom, listRoom} from '../../api/api';

    export default {
		data:function() {
			return {
				filters: {
                    roomName:null
				},
                rooms: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
                    id: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    roomName: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    mobile: [
                        { required: true, message: '11', trigger: 'blur' },
                        {
                            validator: function (rule, val, callback) {
                                if (val.length != 11 || !Number.isInteger(val/1)){
                                    callback(new Error());
                                    return;
                                }
                                callback();
                            },
                            message: '11',
                            trigger: 'blur'
                        }
                    ],
                    url: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    way: [
                        { required: true, message: '', trigger: 'blur',type: 'number' }
                    ]
				},
				//
				editForm: {
					id: null,
                    roomName: '',
					way:null,
                    mobile:null,
                    startHour:0,
                    endHour:23,
                    intervalTime:null,
					url:null,
                    defaultContent:null
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
                    roomName: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    url: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    mobile: [
                        { required: true, message: '11', trigger: 'blur' },
                        {
                            validator: function (rule, val, callback) {
                                if (val.length != 11 || !Number.isInteger(val/1)){
                                    callback(new Error());
                                    return;
                                }
                                callback();
                            },
                            message: '11',
                            trigger: 'blur'
                        }
                    ],
                    way: [
                        { required: true, message: '', trigger: 'blur',type: 'number' }
                    ]
				},
				//
				addForm: {
                    roomName: '',
                    way:null,
                    intervalTime:null,
                    mobile:null,
                    startHour:0,
                    endHour:23,
                    url:null,
                    defaultContent:null
				},
				ways:[
					{key:"NORMAL",id:1,desc:""} , {key:"AUTO",id:2,desc:""},{key:"STOP",id:3,desc:""},{key:"STOP",id:4,desc:""}
				],
				hours:[{desc:0,id:0},{desc:1,id:1},{desc:2,id:2},{desc:3,id:3},{desc:4,id:4},{desc:5,id:5},
					{desc:6,id:6},{desc:7,id:7},{desc:8,id:8},{desc:9,id:9},{desc:10,id:10},{desc:11,id:11},
					{desc:12,id:12},{desc:13,id:13},{desc:14,id:14},{desc:15,id:15},{desc:16,id:16},{desc:17,id:17},
					{desc:18,id:18},{desc:19,id:19},{desc:20,id:20},{desc:21,id:21},{desc:22,id:22},{desc:23,id:23}
				]

			}
		},
		methods: {
            getWayDesc:function (row, column) {
                for(var i=0 , len = this.ways.length; i < len ; i++){
                    if(row.way == this.ways[i].id){
                        return this.ways[i].desc;
					}
				}
                return '';
            },
			handleCurrentChange:function(val) {
				this.page = val;
				this.getRooms();
			},
			//
			getRooms:function() {
				let params = {
					pageNum: this.page,
                    pageSize:10,
                    roomName: this.filters.roomName
				};
				this.listLoading = true;
				NProgress.start();
                listRoom(params , this).then((res) => {
                    if(!res){
                        return;
					}
					this.total = res.total;
					this.rooms = res.data.data;
					this.listLoading = false;
					NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {

			    var _this = this;
			    util.Msg.warning(_this , null , function () {
                    _this.listLoading = true;
                    NProgress.start();
                    let para = { ids: row.id };
                    delRoom(para).then(function() {
                        _this.listLoading = false;
                        NProgress.done();
                        util.Msg.success(_this);
                        _this.getRooms();
                    });
                });

			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);

			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
			},
			//
			editSubmit: function () {
                var _this = this;
				this.$refs.editForm.validate((valid) => {
					if (valid) {
					    util.Msg.confirm(_this , null ,function () {
                            _this.editLoading = true;
                            NProgress.start();
                            let para = Object.assign({}, _this.editForm);
                            editRoom(para).then(function() {
                                _this.editLoading = false;
                                NProgress.done();
                                util.Msg.success(_this);
                                _this.$refs['editForm'].resetFields();
                                _this.editFormVisible = false;
                                _this.getRooms();
                            });
                        });
					}
				});
			},
			//
			addSubmit: function () {
                var _this = this;
				this.$refs.addForm.validate((valid) => {
					if (valid) {
                        util.Msg.confirm(_this , null ,function () {
                            _this.addLoading = true;
                            NProgress.start();
                            let para = Object.assign({}, _this.addForm);
                            addRoom(para).then(function() {
                                _this.addLoading = false;
                                NProgress.done();
                                util.Msg.success(_this);
                                _this.$refs['addForm'].resetFields();
                                _this.addFormVisible = false;
                                _this.getRooms();
                            });
                        });
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
                var _this = this;
                util.Msg.warning(_this , null ,function () {
                    _this.listLoading = true;
                    NProgress.start();
                    let para = { ids: ids };
                    delRoom(para).then(function() {
                        _this.listLoading = false;
                        NProgress.done();
                        util.Msg.success(_this);
                        _this.getRooms();
                    });
                });

			}
		},
		mounted() {
			this.getRooms();
		}
	}

</script>

<style scoped>

</style>


